iframe {
  display: none;
}

body,
h1,
h2,
h3,
h4,
p,
a,
button,
ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

* {
  box-sizing: border-box;
}

button {
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
}
